<template>
    <table class="table" v-bind:class="tableContent.settings.tableClasses">
        <!-- Set table head -->
        <thead>
            <tr>
                <th v-for="column in tableContent.settings.tableColumns" :key="column.field" scope="col"
                    v-bind:class="[column.align, column.colSize]">{{ column.name }}</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="row in tableContent.result" :key="row.id">

                <template v-for="(element, index) in row" :key="JSON.stringify(element)">
                    <!-- Create first column -->
                    <th v-if="index == 0" scope="row"
                        v-bind:class="[tableContent.settings.tableColumns[index].align, tableContent.settings.tableColumns[index].color, tableContent.settings.tableColumns[index].weight, tableContent.settings.tableColumns[index].colSize]">
                        <router-link
                            :to="'/' + tableContent.settings.tableColumns[index].type + '/' + element">{{ element }}</router-link>
                    </th>
                    <!-- Create all other columns -->
                    <td v-if="index != 0" :key="JSON.stringify(element)"
                        v-bind:class="[tableContent.settings.tableColumns[index].align, tableContent.settings.tableColumns[index].color, tableContent.settings.tableColumns[index].weight, tableContent.settings.tableColumns[index].colSize]">

                        <!-- link collumn -->
                        <template v-if="isLink(tableContent.settings.tableColumns[index])">
                            <a v-bind:href="tableContent.settings.tableColumns[index].hrefPrefix + element.href + tableContent.settings.tableColumns[index].hrefSuffix"
                                v-bind:title="tableContent.settings.tableColumns[index].titlePrefix + element.title + tableContent.settings.tableColumns[index].titleSuffix">{{ tableContent.settings.tableColumns[index].textPrefix }}{{ element.text }}{{ tableContent.settings.tableColumns[index].textSuffix }}</a>
                        </template>

                        <!-- Router column -->
                        <template
                            v-else-if="isRouter(tableContent.settings.tableColumns[index]) && element.href != null">
                            <routerLink class="text-decoration-none notnull"
                                :to="tableContent.settings.tableColumns[index].hrefPrefix + element.href + tableContent.settings.tableColumns[index].hrefSuffix">
                                {{ tableContent.settings.tableColumns[index].textPrefix }}{{ element.text }}{{ tableContent.settings.tableColumns[index].textSuffix }}
                            </routerLink>
                        </template>

                        <template
                            v-else-if="isRouter(tableContent.settings.tableColumns[index]) && element.href == null">
                            <routerLink class="text-decoration-none null"
                                :to="tableContent.settings.tableColumns[index].nullHref">
                                {{ tableContent.settings.tableColumns[index].nullText }}</routerLink>
                        </template>

                        <!-- Date column -->
                        <template v-else-if="isDate(tableContent.settings.tableColumns[index])">
                            {{ formatDate(element) }}
                        </template>

                        <template v-else-if="isExternalData(tableContent.settings.tableColumns[index])">
                            {{ getExternalData(tableContent.settings.tableColumns[index], element) }}
                        </template>

                        <!-- else just print content -->
                        <template v-else>
                            {{ element }}
                        </template>
                    </td>
                </template>

            </tr>
        </tbody>
    </table>
    <paginate v-if="pagination == true" v-model="page" :page-count="pageCount" :click-handler="clickCallback"
        :prev-text="'Prev'" :next-text="'Next'" :container-class="'pagination'" :page-class="'page-item'">
    </paginate>
</template>

<script>
import Paginate from 'vuejs-paginate-next';
export default {
    name: 'TableElement',
    props: {
        fetchLink: { type: String, required: true },
        tableColumns: { type: Object, required: true },
        tableClasses: { type: String, required: true },
        pagination: { type: Boolean, required: false }
    },
    watch: {
        fetchLink: function (newVal, oldVal) { // watch it
            console.log('Prop changed: ', newVal, ' | was: ', oldVal)
            this.getList()
        },
        page: function (newVal, oldVal) { // watch it
            console.log('value changed: ', newVal, ' | was: ', oldVal)
            if (this.paginationUsed == true) {
                this.getList()
            }
        },
    },
    data() {
        // Api Specification
        // https://gitlab.com/inno24/web-client/-/wikis/Components/Table
        return {
            tableContent: {
                loaded: false,
                fetchedData: {},
                result: [],
                settings: {
                    tableColumns: this.tableColumns,
                    tableClasses: this.tableClasses,
                }
            },
            paginationUrl: "",
            paginationUsed: false,
            page: 1,
            pageCount: 1
        }
    },
    components: {
        paginate: Paginate
    },
    methods: {
        // Check if the given field is a string or a link object
        isLink(element) {
            if (element.isLink) {
                return true;
            }
            return false;
        },
        isRouter(element) {
            if (element.isRouter) {
                return true;
            }
            return false;
        },
        isDate(element) {
            if (element.isDate) {
                return true;
            }
            return false;
        },
        isExternalData(element) {
            if (element.isExternalData) {
                return true;
            }
            return false;
        },
        formatDate(element) {
            const date = new Date(element).toLocaleDateString('de-AT', {
                day: '2-digit',
                month: '2-digit',
                year: 'numeric',
            });
            return date
        },
        getList() {
            const thisComponent = this;
            thisComponent.fetchedData = {}
            this.tableContent = {
                loaded: false,
                fetchedData: {},
                result: [],
                settings: {
                    tableColumns: this.tableColumns,
                    tableClasses: this.tableClasses,
                }
            }
            var url = ""
            if (this.pagination == true) {
                if (this.paginationUsed != true) {
                    this.page = parseInt(this.$route.query.page ?? 1, 10)
                }
                url = `${thisComponent.fetchLink}?page=${this.page}`
            } else {
                url = this.fetchLink
            }
            console.log("Url to fetch: " + url)
            fetch(url, {
                credentials: 'include',
            })
                .then(function (response) {
                    return response;
                })
                .then(function (data) {
                    return data.json();
                })
                .then(function (data) {
                    thisComponent.tableContent.fetchedData = data;
                    var fetchedData = thisComponent.tableContent.fetchedData;
                    thisComponent.pageCount = Math.ceil(fetchedData.total / 20)
                    for (var index in fetchedData.entry) {

                        var row = [];
                        for (var column of thisComponent.tableContent.settings.tableColumns) {
                            if (column.isLink || column.isRouter) {

                                var titleValue = null;
                                var hrefValue = null;
                                var textValue = null;

                                if (typeof column.titlePath !== 'undefined') {
                                    titleValue = thisComponent.getInObject(fetchedData.entry[index], column.titlePath)
                                }
                                if (typeof column.hrefPath !== 'undefined') {
                                    hrefValue = thisComponent.getInObject(fetchedData.entry[index], column.hrefPath)
                                }
                                if (typeof column.textPath !== 'undefined') {
                                    textValue = thisComponent.getInObject(fetchedData.entry[index], column.textPath)
                                }

                                var link = {
                                    title: titleValue,
                                    href: hrefValue,
                                    text: textValue,
                                }
                                console.log(link)
                                row.push(link);


                            } else {

                                row.push(thisComponent.getInObject(fetchedData.entry[index], column.path));

                            }

                        }

                        thisComponent.tableContent.result.push(row);
                    }

                    thisComponent.tableContent.loaded = true;

                })
                .catch(function (error) {
                    console.error(error);
                });
        },

        getExternalData(collumn, element) {
            if (collumn.data == null) {
                console.log(`[ERROR] no data passed to collumn ${collumn.name}`)
                return ""
            }
            console.log(collumn.data)
            for (const i in collumn.data) {
                console.log(`[INFO] is dataElement.key == element: ${collumn.data[i].key} =? ${element}`)
                if (collumn.data[i].key == element) {
                    return collumn.data[i].value;
                }
            }
            console.log(`[WARNING] data missing in collumn ${collumn.name} for element ${element}`)
            return "no data"
        },

        getInObject(obj, path, def) {
            /**
            * If the path is a string, convert it to an array
            * @param  {String|Array} path The path
            * @return {Array}             The path array
            */
            var stringToPath = function (path) {
                // If the path isn't a string, return it
                if (typeof path !== 'string') {
                    return path;
                }

                if (path.startsWith("_")) {
                    def = path.substring(1)
                    return def;
                }
                // Create new array
                var output = [];

                // Split to an array with dot notation
                path.split('.').forEach(function (item) {

                    // Split to an array with bracket notation
                    item.split(/\[([^}]+)\]/g).forEach(function (key) {

                        // Push to the new array
                        if (key.length > 0) {
                            output.push(key);
                        }

                    });

                });

                return output;
            };

            // Get the path as an array
            path = stringToPath(path);

            // Cache the current object
            var current = obj;

            // For each item in the path, dig into the object
            for (var i = 0; i < path.length; i++) {

                // If the item isn't found, return the default (or null)
                if (!current[path[i]]) return def;

                // Otherwise, update the current  value
                current = current[path[i]];

            }

            return current;

        },
        clickCallback: function (pageNum) {
            console.log(pageNum)
            this.paginationUsed = true
            this.$router.push('patients?page=' + pageNum)
        },

    },
    beforeMount() {
       
        this.getList()
        console.log(this.tableContent)
    },
    mounted() {

    },
}
</script>

<style scoped>
.pagination {
    display: flex;
    justify-content: center;
}
</style>