<script>
//TO DO: Datenbankverbindung bearbeiten
// http://hapi.fhir.org/baseR4/DocumentReference?subject=2819202&contenttype=application/pdf'
import Accordion from '../../components/accordion/Accordion.vue';

const searchedActivityCode = "16076005"

export default ({
    name: "Patient",
    components: {
        Accordion
    },
    data() {
        return {
            patientId: this.$route.params.id,
            patient: {},
            carePlans: {},
            dischargeSummaryStatusList: [],
            accordionIsExpanded: false
        }
    },
    methods: {
        formatDate(element) {
            const date = new Date(element).toLocaleDateString('de-AT', {
                day: '2-digit',
                month: '2-digit',
                year: 'numeric',
            });
            return date
        },
        getAndfilterCarePlans() {
            const thisComponent = this;
            console.log(`${this.$apiUrl}/CarePlan?subject=${this.patientId}&activity-code=${searchedActivityCode}`)
            fetch(`${this.$apiUrl}/CarePlan?subject=${this.patientId}&activity-code=${searchedActivityCode}`, {
                credentials: 'include',
            })
                .then(response => response.json())
                .then(function (data) {
                    console.log(data)
                    if (data.entry == null) {
                        console.log("[ERROR] this.carePlans.entry == null")
                        return
                    }
                    for (const carePlan of data.entry) {
                        if (carePlan.resource.activity == null) {
                            console.log("[ERROR] carePlan.resource.activity == null")
                            continue
                        }
                        for (const activity of carePlan.resource.activity) {
                            var activityFound = false
                            if (activity.detail.code.coding == null) {
                                console.log("[ERROR] activity.detailcode.coding == null")
                                continue
                            }
                            for (const code of activity.detail.code.coding) {
                                if (code.code == searchedActivityCode) {
                                    activityFound = true
                                    break;
                                }
                            }
                            if (activityFound) {
                                console.log(`[INFO] Current Activity.detail: `)
                                console.log(activity.detail)
                                var newSatus = {
                                    key: activity.detail.reasonReference[0].id,
                                    value: activity.detail.status
                                }
                                thisComponent.dischargeSummaryStatusList.push(newSatus)
                                console.log(`Element added to dischargeSummaryStatusList: ["${activity.detail.reasonReference[0].id}, ${activity.detail.status}]`)
                            }
                        }
                    }
                })
        }
    },
    beforeMount() {
        this.getAndfilterCarePlans()
        if (this.$AuthorizedRole([4])) {
            this.accordionIsExpanded = true;
        }
    },
    mounted() {
        fetch(`${this.$apiUrl}/Patient/` + this.patientId, {
            credentials: 'include',
        })
            .then(response => response.json())
            .then(data => this.patient = data);
    }
})
</script>

<template>
    <main id="patient" class="text-start">
        <section>
            <div class="row">
                <div class="col-lg-4">
                    <div class="sticky pt-7">
                        <div class="card card-body text-left mb-3">
                            <template v-if="patient.id != null">
                                <p class="mb-0 text-muted">ID:{{ patient.id }}</p>
                                <h5 class="mb-3 fw-bold">{{
                                    patient.name[0].given[0] + " " +
                                        patient.name[0].family
                                }}<span class="text-muted"> ({{ patient.gender }})</span></h5>
                                <p class="mb-2"><span class="text-muted">Geburtsdatum:
                                    </span>{{ formatDate(patient.birthDate) }}</p>
                                <p class="mb-2"><span class="text-muted">Adresse: </span>{{
                                    patient.address[0].line[0]
                                        + ", " + patient.address[0].city
                                }}</p>
                                <p class="mb-2"><span class="text-muted">Telefonnummer:
                                    </span>{{ patient.telecom[0].value }}</p>
                            </template>
                        </div>
                        <div class="" v-if="patient.id != null && $AuthorizedRole([2])">
                            <button type="button" class="btn btn-primary">
                                <router-link :to="'/patient/' + patient.id + '/createPrescription'">
                                    <vue-feather class="align-middle icon-margin" type="file-plus"></vue-feather>
                                    <span class="align-middle">Neues Rezept</span>
                                </router-link>
                            </button>
                        </div>
                        <div class="" v-if="patient.id != null && $AuthorizedRole([1])">
                            <button type="button" class="btn btn-primary">
                                <router-link :to="'/patient/' + patient.id + '/createDischargeSummary'">
                                    <vue-feather class="align-middle icon-margin" type="file-plus"></vue-feather>
                                    <span class="align-middle">Neuer Entlassungsbrief</span>
                                </router-link>
                            </button>
                        </div>
                    </div>
                </div>

                <div class="col-lg-8">
                    <div class="p-4">
                        <h1 class="mb-3 text-center">Konsultationsverlauf</h1>
                    </div>
                    <div class="card overflow-hidden mb-4">
                        <Accordion v-bind:AccordionItems="
                        [
                            {
                                _id: 'Entlassungsbriefe',
                                authorizedRoles: [1, 2, 3],
                                headline: '📄 Entlassungsbriefe',
                                component: 'AccordionItemTable',
                                tableData: {
                                    fetchLink: `${this.$apiUrl}/Composition?subject=${this.patientId}&type=18842-5`,
                                    tableClasses: 'text-start table-striped',
                                    tableColumns:
                                        [
                                            {
                                                name: 'ID',
                                                field: 'id',
                                                path: 'resource.id',
                                                align: 'text-start',
                                                weight: 'fw-light',
                                                type: `patient/${this.patientId}/discharge-summary`
                                            },
                                            {
                                                name: 'Author',
                                                field: 'beschreibung',
                                                path: 'resource.author[0].display',
                                                align: 'text-start',
                                            },
                                            {
                                                name: 'Datum',
                                                field: 'datum',
                                                path: 'resource.meta.lastUpdated',
                                                align: 'text-start',
                                                color: 'text-muted',
                                                weight: '',
                    
                                                isDate: true
                                            },
                                            {
                                                name: 'Status',
                                                field: 'status',
                                                align: 'text-center',
                                                color: 'text-muted',
                                                weight: '',
                                                path: 'resource.id',
                    
                                                isExternalData: true,
                                                data: dischargeSummaryStatusList
                    
                                            },
                                        ]
                                }
                            },
                            {
                                _id: 'Laufende-Rezepte',
                                authorizedRoles: [1, 2, 3, 4],
                                expanded: accordionIsExpanded,
                                headline: '📝 Laufende Rezepte',
                                component: 'AccordionItemTable',
                                tableData: {
                                    fetchLink: `${this.$apiUrl}/MedicationRequest?subject=${this.patientId}&status=active`,
                                    tableClasses: 'text-start table-striped',
                                    tableColumns:
                                        [
                                            {
                                                name: 'ID',
                                                field: 'id',
                                                path: 'resource.id',
                                                align: 'text-start',
                                                weight: 'fw-light',
                                                type: `patient/${this.patientId}/prescription`,
                                            },
                                            {
                                                name: 'Medikament',
                                                field: 'medikament',
                                                path: 'resource.medicationCodeableConcept.coding[0].display',
                                                align: 'text-start',
                                            },
                                            {
                                                name: 'Priorität',
                                                field: 'prioritaet',
                                                path: 'resource.priority',
                                                align: 'text-center',
                                            },
                                            {
                                                name: 'Datum',
                                                field: 'datum',
                                                path: 'resource.meta.lastUpdated',
                                                align: 'text-end',
                                                color: 'text-muted',
                    
                                                isDate: true
                                            },
                                        ]
                                }
                            },
                            {
                                _id: 'Vergangene-Medikation',
                                authorizedRoles: [1, 2, 3],
                                headline: '💊 Vergangene Medikation',
                                component: 'AccordionItemTable',
                                tableData: {
                                    fetchLink: `${this.$apiUrl}/MedicationRequest?subject=${this.patientId}&status=completed`,
                                    tableClasses: 'text-start table-striped',
                                    tableColumns:
                                        [
                                            {
                                                name: 'ID',
                                                field: 'id',
                                                path: 'resource.id',
                                                align: 'text-start',
                                                weight: 'fw-light',
                                                type: `patient/${this.patientId}/prescription`,
                                            },
                                            {
                                                name: 'Medikament',
                                                field: 'medikament',
                                                path: 'resource.medicationCodeableConcept.coding[0].display',
                                                align: 'text-start',
                                            },
                                            {
                                                name: 'Priorität',
                                                field: 'prioritaet',
                                                path: 'resource.priority',
                                                align: 'text-center',
                                            },
                                            {
                                                name: 'Datum',
                                                field: 'datum',
                                                path: 'resource.meta.lastUpdated',
                                                align: 'text-end',
                                                color: 'text-muted',
                    
                                                isDate: true
                                            },
                                            {
                                                name: 'Status',
                                                field: 'status',
                                                path: 'resource.status',
                                                align: 'text-center',
                    
                                            },
                                        ]
                                }
                            }
                        ]" />
                    </div>
                </div>
            </div>
        </section>
    </main>
</template>



<style scoped>
.sticky {
    position: -webkit-sticky;
    position: sticky !important;
    ;
    top: 80px;
}

.card {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: none;
    box-shadow: 0 0.5rem 1rem rgb(0 0 0 / 15%);
    border-radius: 1rem;
}

.pt-7 {
    padding-top: 7rem !important;
}

.icon-margin {
    margin-right: 0.3rem;
}

button>a,
button>a:hover,
button>a:focus,
button>a:active {
    color: inherit;
    text-decoration: none;
}
</style>
